body {
  margin: 0;
  background-color: #fff;
  color: #2a2a2a;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: inherit;
}
ul {
  list-style-type: none;
}
h1,
h2,
h3,
p {
  margin-top: 0;
  margin-bottom: 0;
}